"use client"

import { useState, useEffect } from 'react'
import Coins from '../components/coins'
import SpreadIndicator from '../components/SpreadIndicator'
import ImbalanceIndicator from '../components/ImbalanceIndicator'
import MarketDepthChart from '../components/MarketDepthChart'
import CandlestickChart from '../components/CandlestickChart'
import { fetchOrderbook, fetchOhlcData } from '../lib/api'

export default function Home() {
  const [selectedCurrency, setSelectedCurrency] = useState('BTCUSDT')
  const [orderbookData, setOrderbookData] = useState({ bids: [], asks: [] })
  const [ohlcData, setOhlcData] = useState([])

  const fetchData = async () => {
    try {
      const [orderbook, ohlc] = await Promise.all([
        fetchOrderbook(selectedCurrency),
        fetchOhlcData(selectedCurrency)
      ])
      setOrderbookData(orderbook)
      setOhlcData(ohlc)
    } catch (error) {
      console.error("Error fetching data:", error)
    }
  }

  useEffect(() => {
    fetchData()
    const interval = setInterval(fetchData, 10000)
    return () => clearInterval(interval)
  }, [selectedCurrency])

  return (
    <div className="space-y-8 p-4">
      <h1 className="text-3xl font-bold mb-8 text-center text-gray-800 dark:text-white">Crypto Dashboard</h1>
      <Coins onCurrencyChange={setSelectedCurrency} />
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <SpreadIndicator orderbookData={orderbookData} />
        <ImbalanceIndicator orderbookData={orderbookData} />
      </div>
      <MarketDepthChart selectedCurrency={selectedCurrency} />
      <CandlestickChart ohlcData={ohlcData} currencyPair={selectedCurrency} fetchNewData={fetchData} />
    </div>
  )
}